:host {
  @include reset-host;
}

:host .experience-zone-card {
  display: flex;
  flex-direction: column-reverse;
  min-width: rem(165);
  height: rem(270);
  overflow: hidden;
  border-radius: rem(12);

  @include mq('min', 'md') {
    min-width: rem(372);
    height: rem(430);
  }

  &:hover {
    .image {
      transform: scale(1.05);
    }
  }
}

:host-context(button:focus) .card-title {
  background-color: $gray-800;
}

:host-context(button:focus) .image {
  transform: scale(1.05);
}

:host .card-title {
  display: box;
  flex-shrink: 0;
  min-width: rem(165);
  height: rem(108);
  padding: rem(24) rem(16);
  text-align: center;
  background-color: $gray-900;

  @include mq('min', 'md') {
    min-width: rem(372);
    height: rem(144);
    padding: rem(40);
  }

  .bs-text {
    height: 100%;
    overflow: hidden;
    font-size: font-size(medium);
  }
}

:host .card-image {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

:host .image {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease-in-out;
}

:host .video-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  padding: 0.5em;
  font-size: rem(32);
  color: $white;
  background: transparent;
  border: 4px solid $white;
  border-radius: 50%;
  transition: $transition-smooth;
  transition-property: transform;
  transform: translate(-50%, -50%);
}

:host([singular]) .experience-zone-card {
  flex-direction: row;
  width: 100%;
}

:host([singular]) .card-title {
  width: rem(165);
  height: 100%;

  @include mq('min', 'md') {
    width: rem(372);
  }

  .bs-text {
    display: flex;
    align-items: center;
  }
}
