import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { BsImage } from '../../atoms/image/image';
import { TextColor } from '../../atoms/text/text.types';
import { IconName } from '../../atoms/icon/icon.types';

/**
 * Experience Zone card component.
 */
@Component({
  tag: 'bs-experience-zone-card',
  styleUrl: 'experience-zone-card.scss',
  shadow: true,
})
export class BsExperienceZoneCard {
  /**
   * Should the layout for singular items be applied?
   */
  @Prop({ reflect: true }) singular?: boolean = false;

  /**
   * The Experience Zone card title.
   */
  @Prop() cardTitle: string;

  /**
   * The Experience Zone card image.
   */
  @Prop() cardImage: BsImage | string;

  @Memoize('cardImage')
  get parsedCardImage(): BsImage {
    return parseIfString(this.cardImage);
  }

  /** Should the image have a play icon? */
  @Prop() video?: boolean = false;

  render() {
    return (
      <Host>
        <div class="experience-zone-card">
          <div class="card-title">
            <bs-text class="bs-text" text={this.cardTitle} color={TextColor.Default} />
          </div>
          <div class="card-image">
            <bs-image
              class="image"
              cover={true}
              src={this.parsedCardImage.src}
              srcset={this.parsedCardImage.srcset}
              imageSizes={this.parsedCardImage.imageSizes}
              altText={this.parsedCardImage.altText}
            />
            {this.video && <bs-icon class="video-icon" name={IconName.Play} />}
          </div>
        </div>
      </Host>
    );
  }
}
